import React from "react"
import { graphql, Link } from "gatsby"

export default function BlogPost({ data }) {
  const post = data.markdownRemark

  return (
    <>
      <Link to="/">
        <div className="bg-gray-light dark:bg-gray-dark p-8 lg:text-2xl text-xl">
          &lt;- Back
        </div>
      </Link>
      <div className="lg:py-32 p-8 bg-white dark:bg-black">
        <div className="container mx-auto">
          <h1 className="lg:text-5xl text-3xl">{post.frontmatter.title}</h1>
          <p className="lg:text-3xl text-xl">{post.frontmatter.date}</p>
          <div className="post lg:text-xl text-lg" dangerouslySetInnerHTML={{ __html: post.html }} />
        </div>
      </div>
    </>
  )
}

export const query = graphql`
  query BlogQuery($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      frontmatter {
        title
        date(formatString: "Do MMMM YYYY")
      }
    }
  }
`
